.Navbar-Principal{  
  width: 100%;
  padding: 30px 100px;
  list-style-type: none;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  position:fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  transition: 0.25s;
}

.whiteNav .Navbar-Principal{
  background: rgba(255,255,255,0.8);
  padding: 10px 100px;
  box-shadow: 0px 5px 20px rgba(3, 3, 3, 0.2);
}
.whiteNav .Navbar-Principal , .whiteNav .navbar-name, .whiteNav .nav-links , .whiteNav .nav-bars , .whiteNav .fa-times , .whiteNav .fa-bars {
  color: black;
}

.navbar-name{
  color: #fff;
  justify-content: center;
  align-self: center;
  justify-self: start;
  margin-left: 0;
}
/*Icone de Menu s√≥ vai estar disponivel se o site estiver no celular ou em tela pequena*/
.menu-icon{
  display: none;
}
.fa-times{
  color: white;
}
.fa-bars{
  color: white;
}
.nav-menu{
  justify-content: end;
  display: grid;
  grid-template-columns: repeat(5,auto);
  grid-gap: 15px;
  list-style: none;
  width: 91vw;
}

@media screen and (max-width: 1500px) {
  .nav-menu{
    width: 87vw;
  }
}

@media screen and (max-width: 1280px) {
  .nav-menu{
    width: 85vw;
  }
}
.nav-links{
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
}
.nav-links:hover{
  background-color: #00ff2a;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s ease-out;
}
@media screen and (max-width: 950px) {
  .Navbar-Principal{
    position: fixed;
  }
  .menu-icon{
    display: block;
    position: fixed;
    top: 20px;
    right: 1rem;
    cursor: pointer;
    font-size: 1.2rem;
    z-index: 2;
  }
  .nav-menu{
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 0;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }
  .nav-menu.active{
    background: #2f2d2d;
    left: 0;
    transition: all 0.5s ease;
    z-index: 1;
  }
  .nav-links{
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }
  .whiteNav .fa-times{
    color: #fff;
  }
  .whiteNav .nav-links , .whiteNav {
    color: #fff;
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }
  .nav-links:hover{
    background-color: #00ff2a;
    border-radius: 0px;
  }
}

