.home{
  top: 0 ;
  background-image: url('https://wallpapercave.com/wp/wp2730030.gif');
  /*background-image: url('https://cdn.dribbble.com/users/543872/screenshots/3440651/untitled-6.gif');*/
  /*background-image: url('https://wallpapercave.com/wp/wp2729923.gif');*/
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
  display: flex;
  flex-direction: column;
  left: 0;
  width: 100vw;
  height: 100vh;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  text-align: center;
  vertical-align: middle;
  top: 0;
}
.name{
  text-align: center;
  font-size: 3rem;
  font-weight: 500;
  color: white;
  line-height: 1.5em;
}
.work{
  text-align: center;
  font-size: 1.5em;
  color: white;
}
.minha-foto{
  margin-top: 22vh;
  margin-left: auto;
  margin-right: auto;
  height: 30vh;
  border-radius: 40%;
}
.social{
  color: white;
  font-size: 50px;
}
.social:hover{
  color: #7dee32;
  transition: all 0.2s ease-out;
} 
.menu{
  padding-top: 1rem;
  display: grid;
  grid-template-columns: repeat(4,auto);
  grid-gap: 20px;
  list-style: none;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: 950px) {
  .home{
    width: 100%;
  }
}